#main-content > .grid-container > .grid-row {
  border: 1px solid #555;
  border-radius: 1rem;

}

#root{
  background-image: url("https://img.freepik.com/free-vector/medical-background-with-flat-icons-symbols_120542-339.jpg?w=2000");
  background-repeat: no-repeat;
  background-size:cover;
}
.display-inline-block{
  color:#074b9aec;
  
}
#Navbar {
  margin-bottom: 0.5rem;
}
#ContentDiv {
  background-color: #ffffff;
  border-radius: 0.5rem;
}
.shoPageSection {
  height: 55vh;
  overflow-y: auto;
}
#buttonsSection {
  justify-content: space-between;
  /* padding: 1rem 2rem; */
  border-top: 1px solid #cfd6dd;
}
#buttonsSection > button {
  margin-bottom: 0.5rem;
}

@media (min-width: 64em) {
  .side-menu-list {
    list-style-type: none;
    color: #555555;
  }
  .side-menu-list > .list {
    /* padding: 10px 10px; */
    font-size: 1rem;
    border-left: 2px solid #cfd6dd;
    color: #000;
  }
}

.side-menu-list {
  list-style-type: none;
  line-height: 1.7;
}
.side-menu-list > li {
  padding: 10px 20px;
  font-size: 1rem;
}

.active {
  color: #000;
  font-weight: bold;
  color: #0c5f96;
  border-left: 2px solid #0c5f96;
}

.done {
  color: #000;
  font-weight: normal;
  border-left: 2px solid #0c5f96;
}

@media (max-width: 63.938em) {

  .usa-navbar {
    border-bottom: none;
  }

  .shoPageSection {
    height: 100%;
    overflow-y: visible;
  }

  .side-menu-list > li {
    float: left;
    padding: 10px 10px 3px;
  }

  #progressbar {
    background-color: #cfd6dd;
    border-radius: 13px;
    padding: 3px;
    width: 25%;
    float: right;
  }

  .progressbardiv {
    background-color: #0c5f96;
    width: 25%;
    height: 3px;
    border-radius: 10px;
  }
  .progressbardiv25 {
    background-color: #0c5f96;
    width: 25%;
    height: 3px;
    border-radius: 10px;
  }
  .progressbardiv50 {
    background-color: #0c5f96;
    width: 50%;
    height: 3px;
    border-radius: 10px;
  }
  .progressbardiv75 {
    background-color: #0c5f96;
    width: 75%;
    height: 3px;
    border-radius: 10px;
  }
  .progressbardiv100 {
    background-color: #0c5f96;
    width: 100%;
    height: 3px;
    border-radius: 10px;
  }
  .active {
    border-left: none;
    border-bottom: 2px solid #0c5f96;
  }

  .list {
    display: none;
  }

  .done {
    border-left: none;
    border-bottom: 2px solid #0c5f96;
	display: none;
  }
}

#Home {
  line-height: 1.7;
}
#Home p {
  color: #555555;
}

#Home ul {
  color: #555555;
  list-style-type: square;
}

#Home ul > li {
  padding: 0.6rem 0.2rem;
}
.TermsCheckbox {
  font-size: 1rem;
}
.usa-checkbox__label::before {
  height: 1rem;
  width: 1rem;
}
.usa-checkbox__input:checked + .usa-checkbox__label::before {
  /* background-color: #0c5f96; */
  outline: none;
}

